import React from "react";
import './Dashboard.css'
import {ProgressIndicator} from '@fluentui/react/lib/ProgressIndicator';
import {Label, Text} from "@fluentui/react";

class DashBoard extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            info: {cpu: 0, mem: 0, running: 0}
        };
    }


    render() {
        return (
            <div className={"dashboard_list"}>
                <div>
                    <Text variant={"xLarge"}>基本信息</Text>
                    <div style={{display: "flex", flex_direction: "row"}}>
                        <Label disabled>后端地址：</Label><Label>{this.backend_path}</Label>
                    </div>
                    <div style={{display: "flex", flex_direction: "row"}}>
                        <Label disabled>API网关地址：</Label><Label>{this.api_gateway_path}</Label>
                    </div>
                    <div style={{display: "flex", flex_direction: "row"}}>
                        <Label disabled>worker数量：</Label><Label>1</Label>
                    </div>
                </div>


                <div>
                    <Text variant={"xLarge"}>系统负载</Text>
                    <ProgressIndicator label="CPU" description={this.state.info.cpu + "%"}
                                       percentComplete={this.state.info.cpu / 100}/>
                    <ProgressIndicator label="MEM" description={this.state.info.mem * 100 + "%"}
                                       percentComplete={this.state.info.mem}/>
                    <ProgressIndicator label="Containers" description={this.state.info.running.toString()}
                                       percentComplete={this.state.info.running / 100}/>
                </div>
                <div>

                </div>
            </div>

        )
    }

    get_info() {
        fetch(this.backend_path + '/dashboard/summary')
            .then(res => res.json())
            .then(res => {
                this.setState({info: res})
                console.log("info", res)
            })
    }

    componentDidMount() {
        // setInterval(() => {
        //     this.get_info()
        // }, 1000)
        this.get_info()
    }
}

export default DashBoard