import React from "react";
import './Endpoint.css'
import {Nav} from "@fluentui/react/lib/Nav";
import {CommandBar} from "@fluentui/react/lib/CommandBar";
import {ChoiceGroup, Label, PrimaryButton} from "@fluentui/react";
import {Checkbox} from '@fluentui/react';
import {Toggle} from '@fluentui/react/lib/Toggle';
import {TextField} from '@fluentui/react/lib/TextField';


class Endpoint extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            function_list: [],
            current_path: "",
            data: {},
            path: "",
            rs_type: "",
            create: true
        };
    }

    onclick(path) {
        this.setState({current_path: path})
    }


    render() {
        return (
            <div>
                <div className={"ep_header_bar"}/>
                <CommandBar
                    items={[
                        {
                            key: 'newItem',
                            text: 'New',
                            cacheKey: 'myCacheKey', // changing this key will invalidate this item's cache
                            iconProps: {iconName: 'Add'},
                            //onClick: () => this.modeCreate(),
                        },
                        {
                            key: 'upload',
                            text: 'Upload',
                            iconProps: {iconName: 'Upload'},
                            //href: 'https://developer.microsoft.com/en-us/fluentui',
                        },
                        {
                            key: 'share',
                            text: 'Share',
                            iconProps: {iconName: 'Share'},
                            //onClick: () => console.log('Share'),
                        },
                        {
                            key: 'download',
                            text: 'Download',
                            iconProps: {iconName: 'Download'},
                            //onClick: () => console.log('Download'),
                        },
                    ]}
                    //overflowItems={this._overflowItems}
                    //overflowButtonProps={this.overflowProps}
                    farItems={[
                        {
                            key: 'info',
                            text: 'Info',
                            // This needs an ariaLabel since it's icon-only
                            ariaLabel: 'Info',
                            iconOnly: true,
                            iconProps: {iconName: 'Info'},
                            //onClick: () => console.log('Info'),
                        },
                    ]}
                    ariaLabel="Inbox actions"
                    primaryGroupAriaLabel="Email actions"
                    farItemsGroupAriaLabel="More actions"
                />


                <div className="ep_main_frame">
                    <div className={"left_bar"}>


                        <Nav
                            onRenderGroupHeader={(group) => {
                                return <h3>{group.name}</h3>;
                            }}
                            ariaLabel="Nav example with custom group headers"
                            groups={[
                                {
                                    name: 'endpoint',
                                    links:
                                        this.state.function_list.map((i) => {
                                                return {name: i, key: i, target: '_blank'}
                                            }
                                        )

                                }
                            ]}
                            onLinkClick={(i, j) => {
                                this.onclick(j.key)
                            }}
                        />

                    </div>
                    <div>
                        <div className={"ep_right_content"}>
                            <div className={"ep_edit_panel"}>
                                <EndpointEdit create={true}/>
                            </div>
                            <iframe name={"ep_save_iframe"} className={"ep_save_iframe"} title={"ep"}/>
                        </div>
                    </div>

                </div>
            </div>

        )
    }

    componentDidMount() {
        fetch(this.backend_path + '/endpoint/get_all')
            .then(res => res.json())
            .then(json => {
                let temp = []
                for (let i in json) {
                    temp.push(i)
                }
                this.setState({function_list: temp, data: json})
                console.log("/endpoint/get_all")
                console.log(json)
            })
    }
}


class EndpointEdit extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            path: "",
            methods: "GET",
            rs_type: "code",
            data: "",
            auth: "none",
            cache: false,
            all_methods_status: {"GET": true, "POST": false, "PATCH": false, "DELETE": false}
        };
        this.all_methods = []
        for (let i in this.state.all_methods_status) {
            this.all_methods.push(i)
        }
        this.file = null
    }

    updateMethods() {
        let new_method = ""
        for (let i in this.state.all_methods_status) {
            if (true === this.state.all_methods_status[i]) {
                new_method += i + ","
            }
        }
        if (new_method.length > 0) {
            new_method = new_method.substring(0, new_method.length - 1)
        }

        this.setState({methods: new_method})
    }

    onSave() {
        let url = this.backend_path + "/endpoint/create"
        this.postData(url, this.state)
    }

    selectMethod(method, checked) {
        for (let i in this.state.all_methods_status) {
            if (method === i) {
                let all_methods_status = this.state.all_methods_status
                all_methods_status[i] = checked
                break
            }
        }
        this.updateMethods()
    }

    render() {
        return (
            <div>

                <div id="ep_form"
                     className={"ep_form"}
                >
                    {
                        <div className={"endpoint"}>


                            <TextField label="自定义路径"
                                       required
                                       description="不要以/结尾"
                                       prefix="/"
                                       disabled={!this.props.create}
                                       onChange={(i, j) => {
                                           this.setState({
                                               path: "/" + j
                                           })
                                       }}
                            />

                        </div>
                    }

                    <Label required> 请求方法 :</Label>
                    {
                        this.all_methods.map(method => {
                            return <Checkbox label={method}
                                             key={method}
                                             defaultChecked={this.state.all_methods_status[method]}
                                             onChange={(i, j) => {
                                                 this.selectMethod(method, j)
                                             }}
                            />
                        })
                    }

                    <ChoiceGroup defaultSelectedKey={this.state.rs_type}
                                 options={[
                                     {key: 'code', text: 'code'},
                                     {key: 'img', text: 'img'},
                                     {key: 'application/json', text: 'json'},
                                     {key: 'text/plain', text: 'text'},
                                     {key: 'other', text: 'other'}
                                 ]}
                                 label="资源类型:"
                                 required={true}
                                 onChange={(i, j) => this.setState({rs_type: j.key})}
                    />


                    <div>
                        {
                            this.state.rs_type === "code" &&
                            <div>
                                function name
                                <TextField name="function_name" type="text"
                                           onChange={(i, j) => {
                                               this.setState({
                                                   data: j
                                               })
                                           }}
                                />
                            </div>
                        }
                        {
                            this.state.rs_type === "img" &&
                            <div>
                                <img alt={""}/>
                                select file<input name={"file"} type="file"/>
                            </div>
                        }
                        {
                            this.state.rs_type === "application/json" &&
                            <div><textarea name="data">json</textarea></div>
                        }
                        {
                            this.state.rs_type === "text/plain" &&
                            <div>
                                <textarea name="data">text</textarea>
                            </div>
                        }
                        {
                            this.state.rs_type === "other" &&
                            <div>
                                mime<input name={"rs_type"} type="text"/>
                                <input name="file" type="file"/>
                            </div>
                        }
                    </div>

                    <ChoiceGroup defaultSelectedKey={this.state.auth}
                                 options={[
                                     {key: 'none', text: 'none'},
                                     {key: 'cookie', text: 'cookie'},
                                     {key: 'header', text: 'header'}
                                 ]}
                                 label="鉴权:"
                                 required={true}
                                 onChange={(i, j) => {
                                     this.setState({
                                         auth: j.key
                                     })
                                 }}
                    />

                    <Toggle label="缓存" onText="开启" offText="关闭"
                            defaultChecked={this.state.cache}
                            onChange={(i, j) => {
                                this.setState({
                                    cache: j
                                })
                            }}
                    />


                    <div>
                        <PrimaryButton type="button" onClick={() => this.onSave()} text="创建"/>
                    </div>
                </div>
            </div>
        )
    }
}

export default Endpoint