import * as React from 'react';
import {DetailsList, DetailsRow, IDetailsRowStyles, IDetailsListProps} from '@fluentui/react/lib/DetailsList';
import {createListItems, IExampleItem} from '@fluentui/example-data';
import {getTheme} from '@fluentui/react/lib/Styling';
import {TextField} from "@fluentui/react/lib/TextField";
import Row from "@fluent-ui/core/Table/components/Row";


const theme = getTheme();

class StatefulApp extends React.Component {


    constructor(props) {
        super(props);
        this._items = createListItems(500);
        this._items = [{name: 'test', state: 'up', image: 'nginx', port: 80, create_on: '2021.4.3 31:32:34'}]
        console.log(this._items)

    }

    render() {
        return (
            <div>
                <DetailsList items={this._items} setKey="set" onRenderRow={this._onRenderRow}
                             checkButtonAriaLabel="select row"/>
                <div>
                    <CreateStatefulApp/>
                </div>
            </div>

        );
    }

    _onRenderRow(props) {
        const customStyles = {};
        if (props) {
            if (props.itemIndex % 2 === 0) {
                // Every other row renders with a different background color
                customStyles.root = {backgroundColor: theme.palette.themeLighterAlt};
            }
            return <DetailsRow {...props} styles={customStyles}/>;
        }
        return null;
    };
}

class CreateStatefulApp extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div style={{display: 'flex', flex_direction: 'row'}}>
                <div>
                    <label>name</label>
                    <TextField/>
                </div>
                <div>
                    <label>image</label><TextField/>
                </div>
                <div>
                    <label>config</label><TextField/>
                </div>
            </div>
        )
    }
}

export default StatefulApp