import React from "react";
import './Function.css'

import CodeMirror from '@uiw/react-codemirror';
import 'codemirror/keymap/sublime';
import 'codemirror/theme/monokai.css';
import {Nav} from '@fluentui/react/lib/Nav';

import {CommandBar} from '@fluentui/react/lib/CommandBar';
import {Label} from '@fluentui/react/lib/Label';
import {TextField} from '@fluentui/react/lib/TextField';
import {ChoiceGroup, DefaultButton, PrimaryButton, Slider} from "@fluentui/react";
import {Stack} from '@fluentui/react/lib/Stack';

class Function extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            all_function_list: [],
            selected: null,
            test_run_res: null,
            fn_detail: null,
            fn_edit: false,
            fn_create: false,
            fn_summary: false
        }
        this.code = 'def start():\n\treturn("123")\n\n\n\n\n\n\n\n';
    }

    modeCreate() {
        this.setState({
            fn_edit: true,
            fn_create: true,
            fn_summary: false
        })
    }

    modeEdit() {
        this.setState({
            fn_edit: true,
            fn_create: false,
            fn_summary: false
        })
    }

    modeSummary() {
        this.setState({
            fn_edit: false,
            fn_create: false,
            fn_summary: true
        })
    }

    modeClose() {
        this.setState({
            fn_edit: false,
            fn_create: false,
            fn_summary: false
        })
    }

    onSelect(item) {
        console.log("select", item)
        fetch(this.backend_path + '/function/get?function_name=' + item)
            .then(res => res.json())
            .then(res => {
                this.setState({selected: res})
                this.setState({fn_detail: res})
                console.log("get_function", res)
                this.modeSummary()
            })
    }

    render() {
        return (
            <div>
                {/**************************** command bar ****************************/}
                <div>
                    <CommandBar
                        items={[
                            {
                                key: 'newItem',
                                text: 'New',
                                cacheKey: 'myCacheKey', // changing this key will invalidate this item's cache
                                iconProps: {iconName: 'Add'},
                                onClick: () => this.modeCreate(),
                            },
                            {
                                key: 'upload',
                                text: 'Upload',
                                iconProps: {iconName: 'Upload'},
                                //href: 'https://developer.microsoft.com/en-us/fluentui',
                            },
                            {
                                key: 'share',
                                text: 'Share',
                                iconProps: {iconName: 'Share'},
                                //onClick: () => console.log('Share'),
                            },
                            {
                                key: 'download',
                                text: 'Download',
                                iconProps: {iconName: 'Download'},
                                //onClick: () => console.log('Download'),
                            },
                        ]}
                        //overflowItems={this._overflowItems}
                        //overflowButtonProps={this.overflowProps}
                        farItems={[
                            {
                                key: 'info',
                                text: 'Info',
                                // This needs an ariaLabel since it's icon-only
                                ariaLabel: 'Info',
                                iconOnly: true,
                                iconProps: {iconName: 'Info'},
                                //onClick: () => console.log('Info'),
                            },
                        ]}
                        ariaLabel="Inbox actions"
                        primaryGroupAriaLabel="Email actions"
                        farItemsGroupAriaLabel="More actions"
                    />
                </div>

                {/**************************** main frame ****************************/}
                <div className={"fn_main_frame"}>

                    {/**************************** left side ****************************/}
                    <div className="fn_left_side">

                        <Nav
                            onRenderGroupHeader={(group) => {
                                return <h3>{group.name}</h3>;
                            }}
                            ariaLabel="Nav example with custom group headers"
                            groups={[
                                {
                                    name: '函数列表',
                                    links:
                                        this.state.all_function_list.map((i) => {
                                                return {name: i, key: i, target: '_blank'}
                                            }
                                        )

                                }
                            ]}
                            onLinkClick={(i, j) => {
                                this.onSelect(j.key)
                            }}
                        />

                    </div>

                    {/**************************** right side ****************************/}
                    <div className={"fn_right_side"}>
                        <div>
                            <table style={{background_color: "#5f6368"}}/>
                            {/*padding*/}
                        </div>
                        <div>
                            {
                                this.state.fn_summary &&
                                <FunctionPreview fn_detail={this.state.fn_detail} onEdit={() => this.modeEdit()}
                                />
                            }
                        </div>

                        <div>
                            {
                                this.state.fn_edit &&
                                <FunctionEdit fn_create={this.state.fn_create} fn_edit={this.state.fn_edit}
                                              fn_detail={this.state.fn_detail}
                                              code={this.code} cancelEdit={() => this.modeClose()}
                                />
                            }
                        </div>


                    </div>
                </div>
            </div>
        );
    }

    componentDidMount() {
        fetch(this.backend_path + '/function/get_all')
            .then(res => res.text())
            .then(res => {
                res = res.substring(1, res.length - 1)
                res = res.replaceAll("'", "")
                res = res.replaceAll(" ", "")
                res = res.split(',')
                this.setState({all_function_list: res})
                console.log("get_all_function", res)
            })

    }
}

class FunctionPreview extends React.Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    function_test() {
        //let name = document.getElementsByName("function_name")[0].value
        let name = this.props.fn_detail.name
        fetch(this.backend_path + '/function/test_run?function_name=' + name + '&param={}')
            .then(res => res.text())
            .then(res => {
                console.log("test_run", res)
                this.setState({test_run_res: res})
            })
    }

    render() {
        return (<div>
            <div className="fn_detail_content">

                <Stack horizontal>
                    <Stack>
                        <Label disabled>函数名</Label>
                        <Label disabled>函数运行环境</Label>
                        <Label disabled>运行配置</Label>
                        <Label disabled>超时</Label>
                    </Stack>

                    <div style={{"min-width": "20px"}}/>

                    <Stack>
                        <Label>{this.props.fn_detail.name}</Label>
                        <Label>{this.props.fn_detail.language}</Label>
                        <Label>128MB</Label>
                        <Label>60s</Label>
                    </Stack>
                </Stack>


                <div className="detail_code">
                    <code>{this.props.fn_detail.code.replaceAll("\t", "    ")}</code>
                    {/*<code>{this.props.fn_detail.code}</code>*/}
                </div>
                <div>
                    <PrimaryButton text={"编辑"} onClick={this.props.onEdit}/>
                </div>

                <div>
                    <PrimaryButton onClick={() => this.function_test()}>测试函数</PrimaryButton>
                </div>
                <div>
                    {this.state.test_run_res}
                </div>
            </div>
        </div>)
    }
}

class FunctionEdit extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            function_name: "",
            language: "python",
            config: "small",
            timeout: 20,
            code: this.props.code
        }
    }

    onCodeChange(code) {
        this.setState({
            code: code.getValue()
        })
    }

    onSave() {
        let url = this.backend_path + "/function/create"
        this.postData(url, this.state).then(data => {
            console.log(data)
        })

        this.postData("https://www.baidu.com").then(data => {
            console.log(data)
        })
    }

    componentWillMount() {
        if (!this.props.fn_create) {
            this.setState({
                function_name: this.props.fn_detail.name,
                language: this.props.fn_detail.language,
                config: this.props.fn_detail.config,
                timeout: this.props.fn_detail.timeout,
                code: this.props.code
            })
        }
    }

    render() {
        return (<div>
            <div className={"fn_edit_content"}
                // style={{display: (this.props.fn_edit ? "" : "none")}}
            >
                <div className={"fn_content"}>
                    <Label>在此配置函数</Label>
                    <div><Label disable>函数名</Label>
                        <TextField type={"text"} name={"function_name"} disabled={!this.props.fn_create}
                                   value={this.state.function_name}
                                   onChange={(k, v) => {
                                       this.setState({
                                           function_name: v
                                       })
                                   }}/>
                    </div>
                    <div>
                        <ChoiceGroup defaultSelectedKey={this.state.language}
                                     options={[
                                         {key: 'python', text: 'python'},
                                         {key: 'node', text: 'node'}
                                     ]}
                                     label="选择函数运行环境:"
                                     required={true}
                                     onChange={(k, v) => {
                                         this.setState({
                                             language: v.key
                                         })
                                     }}
                        />
                    </div>

                    <div>
                        <ChoiceGroup defaultSelectedKey={this.state.config}
                                     options={[
                                         {key: 'small', text: '128MB'},
                                         {key: 'middle', text: '256MB'},
                                         {key: 'large', text: '512MB'},
                                         {key: 'xlarge', text: '1G'}
                                     ]}
                                     label="选择函数运行配置:"
                                     required={true}
                                     onChange={(k, v) => {
                                         this.setState({
                                             config: v.key
                                         })
                                     }}
                        />
                    </div>
                    <div>
                        <Slider label="超时(s):" min={0} max={120} step={1} defaultValue={this.state.timeout} showValue
                                snapToStep
                                onChanged={(i, j) => {
                                    this.setState({
                                        timeout: j
                                    })
                                }}
                        />
                    </div>

                    <div>
                        <div className={"fn_editor"}>
                            <CodeMirror
                                value={this.props.code}
                                options={{
                                    theme: 'monokai',
                                    keyMap: 'sublime',
                                    mode: 'python'
                                }}
                                onChange={code => this.onCodeChange(code)}
                            />
                            <textarea style={{display: "none"}} name="code" defaultValue={this.state.code}/>
                        </div>
                    </div>

                    <div>
                        <PrimaryButton type={"button"} text={this.props.fn_create ? "创建" : "保存"}
                                       onClick={this.onSave.bind(this)}/>
                        <DefaultButton type={"button"} text={"取消"} onClick={this.props.cancelEdit}/>
                    </div>
                    <div>
                        <iframe name="save_iframe" className={"fn_save_iframe"} title={"save_iframe"}/>
                    </div>
                </div>

            </div>

        </div>)
    }
}

export default Function;
